.App {
    flex-grow: 1;
    /* padding-left: 0;
    margin-left: 0; */
    background-color: #141321;
    width: 100%;
    /* color: #EC407A; */
    padding: 200px;
    padding-top: 10px;
    /* color: #b45776 */
    color: rgb(202, 197, 197);
    /* border: 5px solid red */
}

h3 {
    color: #EC407A;
    text-align: center;
    font-size: 40px;
}

a {
    text-decoration: none;
    color: #EC407A ! important;
}


#nav-menu {
    font-size: 25px;
    display: flex;
    margin: auto;
    justify-content: space-evenly;
    width: 80%;
    font-weight: bold;
    position: fixed;
    transition: all 0.3s ease;
    /* border: 2px solid green; */
}



button {
    /* color: #161616 !important; */
    border: none;
    /* font-weight: bold; */
    /* font-size: 30px;  */
    background-color: #EC407A;
    border-radius: 15px 10px;
    padding: 5px
 } 

#resume-link-2, #resume-link-1{
    color: #161616 !important;
    /* border: none; */
    font-weight: bold;
    font-size: 30px;
    background-color: #EC407A;
    border-radius: 15px 10px;
    padding: 5px
}

button:hover {
    background-color: #E91E63;
    border-radius: 10px 15px;
}

#home {
    display: flex;
    justify-content: space-evenly ;
    padding-top: 150px;
    padding-bottom: 100px;
    align-items: center;
    height: 250px;
    font-size: 30px;
}

#hi {
    height: 50px;
    width: 50px;
}

#home #working {
    height: 100px;
    width: 200px
}

#home #working img {
    height: 250%;
    width: 150%;
    border-radius: 100px;
}



  

#about {
    font-size: 25px;
    padding-top: 50px;
    display: flex;
    padding-bottom: 90px;
    justify-content: space-evenly ;
    
}

#about img {
    width: 300px;
    height: 300px;
    margin-top: 50px;
    border-radius: 50px;
    margin-left: 80px;
}

#skills {
    /* padding-top: 50px; */
    /* border: 2px solid red; */
    /* padding:0; */
}

#skills>div {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
}

#skills img {
    width: 100px;
    height: 100px;
    border-radius: 10px;
    /* object-fit: cover; */
}

#projects {
    padding-top: 50px;
}

#projects>div {
    display: flex;
    justify-content: space-evenly;
    /* border: 2px solid white     */
    text-align: center;
}

.project-card {
    width: 50%;
    height: 650px;
    border-radius: 50px;
    margin: 10px;
    padding: 10px;
    background-color: #000000;
    color: rgb(202, 197, 197);
    /* border: 2px solid red; */
    font-size: 20px;
}

.project-card:hover {
    background-color: #a0556e;
    color: #141321;
    padding: 8px;
}

.project-card:hover a {
    background-color: #141321;
    color: #a0556e !important;
}

.projectLink {
    display: flex;
    justify-content: space-evenly;
}

.project-card a {
    padding: 5px;
    background-color: rgb(197, 197, 88);
    color: #161616 ! important;
    border-radius: 15px;
    width: 100px
}

.projectLink a:hover {
    height: 10px;
    width: 45px;
}

#projects img {
    width: 80%;
    height: 320px;
    /* margin: 30px; */
}

#projects p {
    font-size: 20px;
}

#contact {
    padding-top: 50px;
    font-size: 30px;
}

#contact>div {
    display: flex;
    justify-content: space-between;
}

#contact img {
    width: 40px;
    height: 40px;
    border-radius: 100px;
    margin-right: 30px;

}

#contact a {
    color: rgb(202, 197, 197) !important
}

#contact #contactGif {
    width: 300px;
    height: 300px
}

#github {
    padding-top: 50px;
}

#github>div {
    height: 200px;
    width: 100%;
    display: block;
    
    
    
}

#github img,
#github .react-activity-calendar {
    height: 100%;
    width: 100%;
    justify-self: center;
}

